<template>
  <div class="card-user">
    <span class="card-user_name">
      {{user.name}}
    </span>
    <span class="card-user_score">
      {{getScore()}}
      <i class="material-icons">&#xE885;</i>
    </span>
  </div>
</template>

<script>
export default {
  name: 'score-user',
  props: {
    user: Object,
    datetime: String,
    score: Number
  },
  methods: {
    getScore () {
      let score = 0
      if (this.user) {
        if (this.datetime) {
          return this.user.scores[this.datetime]
        }
        var keys = Object.keys(this.user.scores)
        for (var i in keys) {
          if (this.user.scores[i] > score) {
            score = this.user.scores[i]
          }
        }
      }
      return score
    },
